.sweet-loading{
  display:block;
  background-color: #000000;
  text-align: center;
  z-index: 999;
  position: relative;
}
.App {
  display:block;
  text-align: center; 
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
/*Header*/
.main-bg{
  background-image: url(./images/netflix_bg_1.jpg);
  position: relative;
  background-repeat: no-repeat;
  width: 100%;
  height: 500px;
  z-index: 99;
  background-size:cover;
}
.main-heading{
  display: block;
  background:#000000cf;
  color:#fffbfb;
  position: relative;
  top:20%;
  left:19%;
  border-radius: 2px;
  filter:brightness(120%);
  justify-content:space-between;
  align-items: center;
  padding: 70px 20px;
  text-shadow:1px 1px 20px #ffd5d5;
  box-shadow: 0 0 40px #3e3e3e;
  font-family: 'Poppins';
  width:61%;
  text-align: center;
}

.main-heading span{
  color:#b30000;
  border-bottom:2px #8a0000;
}
.close{
  position: absolute;
  right:10px;
  top:10px;
  width:32px;
  height: 32px;
  opacity: 0.4;
}
.close:hover{
  opacity: 1;
  cursor: pointer;
}
.close:before, .close:after {
  position: absolute;
  left:15px;
  content: '';
  height: 33px;
  width: 2px;
  background-color: rgb(199, 150, 150);
}
.close:before{
  transform: rotate(45deg);
}
.close::after{
  transform: rotate(-45deg);
}

.main-heading span{
  color:rgb(255, 138, 138)
}
.header-btn{
  background-color: #00000069;
  color: rgb(255, 255, 255);
  border:2px solid #e50914;
  text-decoration: none;
  padding: 5px 5px;
  letter-spacing: 1px;
  border-radius: 3px;
  font-size: 0.9rem;
  box-shadow: 2px 2px rgba(34, 19, 19, 0.568);
  cursor: pointer;
}
.header-btn:hover{
  border:2px solid rgb(255, 109, 109);
  text-shadow: 1px 1px 20px #ffd5d5;
}
/*content1*/
.content1-heading{
  display: block;
  background:#000000;
  color:#fffbfb;
  position: relative;
  justify-content:space-between;
  align-items: left;
  padding: 100px 150px;
  text-shadow:2px 2px 3px #3535357e;
  font-family: 'Poppins';
  width:100%;
  text-align: left;
  border-top: 8px solid #ffffff21;
}
.content1-heading span{
  color:#e50914;
  border-bottom:2px #8a0000;
}
.content-details{
  display: flex;
  flex-direction: row;
  font-size: large;
  width:50%;
}
.animation{
  background-color: #000000;
  display: block;
}
.animation-img{
  display: block;
  padding: 50px;
  width: 470px;
  height: auto;
  position: absolute;
  top:0px;
  left:50%;
}
/*content2*/
.content2-heading{
  display: block;
  background:#000000;
  color:#fffbfb;
  position: relative;
  justify-content:space-between;
  align-items: right;
  padding: 100px 150px;
  text-shadow:2px 2px 3px #3535357e;
  font-family: 'Poppins';
  width:100%;
  text-align: right;
  border-top: 8px solid #ffffff21;
}
.content2-heading h1{
  text-align: right;
  width: 100%;
}
.content2-heading span{
  color:#e50914;
  border-bottom:2px #8a0000;
}
.content2-details{
  display: inline-flex;
  font-size: large;
  width:50%;
  text-align: right;
}
.animation2{
  background-color: #000000;
  display: block;
}
.animation-img2{
  display: block;
  padding: 50px;
  width: 470px;
  height: auto;
  position: absolute;
  top:0px;
  left:0;
}
/*content3*/
.content3-heading{
  display: block;
  background:url(./images/assets_bg.png) no-repeat center center;
  background-size: cover;
  color:#fffbfb;
  position: relative;
  justify-content:space-between;
  align-items: right;
  padding: 100px 150px;
  text-shadow:2px 2px 3px #3535357e;
  font-family: 'Poppins';
  width:100%;
  text-align: right;
  border-top: 8px solid #ffffff21;
}
.content3-heading h1{
  text-align: right;
  width: 100%;
}
.content3-details{
  display: inline-flex;
  font-size: large;
  width:50%;
  text-align: right;
}
/*footer*/
.footer{
  display: inline-flex;
  flex-direction: row;
  flex-wrap: nowrap;
  background:linear-gradient(180deg,#3e0000 ,#070000 70%);
  color:#fffbfb;
  position: relative;
  justify-content:center;
  align-items: flex-start;
  text-shadow:2px 2px 3px #3535357e;
  font-family: 'Poppins';
  width:100%;
  text-align: left;
  border-top: 8px solid #6c0000;
  border-bottom: 8px solid rgb(7, 7, 7);
}
.footer-details{
  color: #8a0000;
  display: block;
  font-size: medium;
  text-align: center;
  position: absolute;
  top:77%;
  left:30%;
}
#footer-link{
  color: #757575;
  padding: 50px;
}
@media (max-width:600px){
  #footer-link{
    color: #757575;
    padding: 30px;
    font-size: small;
    border-bottom: 8px solid #ffffff3a;
  }
}
@media (max-width:436px){
  #footer-link{
    color: #757575;
    padding: 20px;
    font-size: x-small;
  }
}
@media (max-width:366px){
  #footer-link{
    color: #757575;
    padding: 10px;
    font-size: xx-small;
  }
}
.footer-li{
  margin-bottom: 20px;
}
.li-style {
  list-style: none;
  margin-left: 0;
  padding: 20px;
  cursor: pointer;
}
.footer-btn{
  background-color: #00000069;
  color: rgba(255, 255, 255, 0.5);
  border:2px solid #ffffff;
  text-decoration: none;
  padding: 5px 5px;
  border-radius: 3px;
  font-size: 0.9rem;
  box-shadow: 2px 2px rgba(34, 19, 19, 0.568);
  cursor: pointer;
  position: absolute;
  top:80%;
  left:59%;
}
.footer-btn:hover{
  border:2px solid rgb(255, 109, 109);
  text-shadow: 1px 1px 20px #ffd5d5;
}
.copyright{
  color: #424242;
}
@media (max-width:1180px){
  .animation-img2{
    width:400px;
    left:0;
    top:7%
  }}
  @media (max-width:1120px){
    .animation-img2{
      width:330px;
      left:0;
      top:15%
    }}
    @media (max-width:1050px){
      .animation-img2{
        display: none;
      }}
@media (max-width:945px){
  .animation-img{
    width:400px;
    left:52%;
    top:4%
  }
}
  @media (max-width:805px){
    .animation-img{
      width:350px;
      left:52%;
      top:8%
    }
}
@media (max-width:775px){
  .animation-img{
    display: none;
  }
}
@media only screen and (max-width:600px) {
  .main-heading{
  max-width: 75%;
  width: 75%;
  height: auto;
  position: absolute;
  top:11%;
  left:13%;
  display: flex;
  flex-direction: column;
  }
  .content1-heading{
    padding: 60px 30px;
  }
  .content-details{
    width: 90%;
  }
  .content2-heading{
    padding: 60px 30px;
  }
  .content2-details{
    width: 90%;
  }
  .content3-heading{
    padding: 60px 30px;
  }
  .content3-details{
    width: 90%;
  }
}