@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&family=Palette+Mosaic&display=swap');
@import url("../css/media.css");
@import url("../css/Preloader.css");

.page {
	width: 100%;
	height: auto;
	flex: 1 1 auto;
	padding: 50px 30px;
	font-family: "Montserrat","Poppins", Tahoma, Geneva, Verdana, sans-serif;
	background: linear-gradient(180deg,#000000 30%,#040404);
	color: #fffbfb;
}

.page__search {
	margin-bottom: 50px;
	font-family: "Comfortaa","Poppins", Tahoma, Geneva, Verdana, sans-serif;
}
.search__wrapp {
	height: 45px;
	display: flex;
	flex-wrap: nowrap;
	justify-content: space-between;
	margin-bottom: 25px;
}
.search__field {
	width: 80%;
	padding: 0 20px;
	border: 2px solid #e50914;
	background-color: #080808;
	border-radius: 3px;
	font-size: 16px;
	box-shadow: 0 0 10px rgba(255, 230, 0, 0.152);
	margin-left: auto;
	margin-right: auto;
	font-family: "Comfortaa","Poppins", Tahoma, Geneva, Verdana, sans-serif;
	color:#fff;
}
.search__field:focus {
	transform: scale(1.01);
}
.search__btn {
	font-family: "Comfortaa", sans-serif;
	text-transform: uppercase;
	font-size: 14px;
	font-weight: bold;
	letter-spacing: 1.5px;
	padding: 0 30px;
	position: relative;
	left:0px;
	margin-left: 10px;
	margin-right: 0px;
}
.search__btn:hover {
	background-color: rgba(70, 70, 70, 0.483);
	transform: scale(1.05);
	border: 2px solid #e50914;
}
.search__btn:active {
color: #e50914;
}
.search__radio-box {
	display: flex;
	flex-wrap: nowrap;
}
.search__radio-item:nth-child(n):not(:last-child) {
	margin-right: 30px;
}

.page__movies {
	margin: 0 -10px;
	display: flex;
	flex-wrap: wrap;
}
.page__movies h2 {
	font-size: 20px;
}

.page__card {
	margin-bottom: 20px;
}
.card {
	padding: 0 10px;
	display: flex;
	flex-direction: column;
	flex: 0 1 33.3333%;
	min-width: 250px;
	border-radius: 10px;
}

.card__item {
	display: flex;
	flex-direction: column;
	flex: 1 0 auto;
	-webkit-box-shadow: 2px 4px 23px -8px #41440033;
	-moz-box-shadow: 2px 4px 23px -8px #41440033;
	box-shadow: 2px 4px 23px -8px #41440033;
	background-color: #3d3d3d80;
	border-radius: 3px;
}
.card__item:hover{
	transform: scale(1.03);
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	transition: ease-in 0.2s;
	border-radius: 5px;
}
.card__item:not(:hover){
	transition: ease-in 0.1s;
}
.card__image {
	width: 100%;
}
.card__image img {
	width: 100%;
	padding: 15px;
	padding-bottom: 0px;
}
.card__description {
	height: 100%;
	display: flex;
	flex-direction: column;
	padding: 15px;
}
.card__title {
	padding: 0 0 10px 0;
	font-size: 17px;
	font-weight: 600;
}
.card__year {
	font-size: 12px;
	color: #ff0000;
}

.card__plot {
	padding: 14px 0;
	font-size: 16px;
	line-height: 20px;
	flex: 1 0 auto;
	font-size: 16px;
}
.card__type {
	display: flex;
	justify-content: flex-end;
	font-style: italic;
	color: rgba(255, 255, 255, 0.153);
	text-transform: uppercase;
	font-size: small;
	font-family: "Comfortaa","Poppins", Tahoma, Geneva, Verdana, sans-serif;
}
@media (min-width:500px){
	.card__plot {
		padding: 14px 0;
		font-size: 16px;
		line-height: 20px;
		flex: 1 0 auto;
		font-size: 15px;
	}
	.card__type {
		display: flex;
		justify-content: flex-end;
		font-style: italic;
		color: rgba(255, 255, 255, 0.153);
		text-transform: uppercase;
		font-size: smaller;
	}
}
@media (max-width: 790px) {
	.page {
		padding: 30px;
	}
	.card {
		flex: 0 1 50%;
	}
}
@media (max-width: 540px) {
	.header {
		padding: 15px;
	}
	.page {
		padding: 20px 25px 0 px;
	}
	.card {
		flex: 0 1 auto;
		width: 100%;
	}
}
@media (max-width: 500px) {
	.header__logo img {
		max-width: 40px;
	}
	.header__title {
		font-size: 22px;
	}
	.header__link {
		font-size: 18px;
	}
	.search__wrapp {
		flex-wrap: wrap;
		height: 100px;
	}
	.page__search {
		margin-bottom: 40px;
	}
	.search__field {
		width: 100% !important;
		margin-bottom: 10px;
		padding: 10px 10px;
	}
	.search__btn {
		margin-left: auto;
		margin-right: auto;
		width: 60%;
		padding: 10px 5px;
	}
	.search__radio-item:nth-child(n):not(:last-child) {
		margin-right: auto;
	}
	.search__radio-item{
		font-size: 12px;
	}
}
@media (max-width: 350px) {
	.header__logo img {
		max-width: 30px;
	}
	.header__title {
		font-size: 20px;
	}
}
