}

/*****Responsive*****/
nav .menu-btn,
.menu-icon{
		display:none;
	}
@media(max-width:900px){
  .menu-icon{
    display:flex;
  }
        nav{
            justify-content: space-between;
            height: 67px;
            padding: 0px 20px;
        }
        .menu{ 
            display:none;
            position:absolute;
            top:67px;
            right:0px;
            background-color:#130000;
            border-bottom:4px solid #8a0000;
            width:100%;
            padding:10px;
            margin:0px;
        }
        .menu li{
            width:100%;
        }
        nav .menu li a{
            font-size: large;
            width:90%;
            height:32px;
            justify-content: left;
            align-items: left;
            margin:0;
            padding: 5px;
            border:1px solid rgba(121, 0, 0, 0.03);
            border-radius: 30px;
        }
        nav .menu-icon{
            cursor:pointer;
            float:right;
            padding:25px 20px;
            position:relative;
            -webkit-user-select: none;
                -ms-user-select: none;
                    user-select: none;
        }
        nav .menu-icon .nav-icon{
            background-color:#ffffff;
            display:block;
            height:2px;
            position:relative;
            transition: background 0.2s ease-out;
            width:18px;
        }
        nav .menu-icon .nav-icon:before,
        nav .menu-icon .nav-icon:after{
            background:rgb(255, 255, 255);
            content:'';
            display:block;
            height:100%;
            position:absolute;
            transition:all ease-out 0.2s;
            width:100%;
        }
        nav .menu-icon .nav-icon:before{
            top:5px;
        }
        nav .menu-icon .nav-icon:after{
            top:-5px;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon{
            background:transparent;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon:before{
            -webkit-transform: rotate(-45deg);
                    transform: rotate(-45deg);
            top:0;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon:after{
            -webkit-transform: rotate(45deg);
                    transform: rotate(45deg);
            top:0;
        }
        nav .menu-btn{
            display:none;
        }
        nav .menu-btn:checked ~ .menu{
            display:block;
        }
}
