@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap');

*{
  font-family: 'Montserrat','Poppins';
  box-sizing: border-box;
}
body{
  margin:0;
  padding: 0;
}

/*HEADER*/
.nav-logo img{
  width:150px;
  height: auto;
  position: relative;
  padding: 2px;
  opacity: 1;
  left:-10px;
}
nav{
  display: flex;
	justify-content: space-around;
	align-items: center;
	box-shadow: 0px 10px 70px rgba(0, 0, 0, 0.356);
	position: relative;
	left: 0;
	top: 0px;
	width:100%;
	z-index: 999;
	background:linear-gradient(180deg,#000000 30%,#030303);
}
nav ul{
	display: flex;
  list-style:none;
}
.active{
    background-color: #b30000;
    color:#ffffff;
    text-shadow:0px 2px 10px #130000;
	  transition: all ease 0.2s;
}

nav ul li a{
  text-decoration: none;
  font-family: 'Montserrat','Poppins';
	height:40px;
	margin: 4px;
	padding: 11px 15px;
	display:inline-flex;
	font-size: 0.8rem;
	text-transform: uppercase;
	font-weight: 400;
	color:#ffffff;
    letter-spacing: 1.35px;
    border-radius: 3px;
    transition: 0.3s ease-in-out;
    text-shadow:0px 2px 5px #130000;
}
nav ul li a:hover{
	background-color: #cf0000;
    color:#ffffff;
	box-shadow: 5px 10px 30px rgba(204, 0, 0, 0.411);
	transition: all ease 0.2s;
}
.icons{
  display: flex;
  position: relative;
  top:-5px;
  padding: 5px 10px;
  color: #ff2727;

}
.icons:hover{
  color:#f8f8f8;
}
input{
  outline: none;
  border: none;
}

/*****Responsive*****/
nav .menu-btn,
.menu-icon{
		display:none;
	}
@media(max-width:900px){
  .menu-icon{
    display:flex;
  }
        nav{
            justify-content: space-between;
            height: 67px;
            padding: 0px 20px;
        }
        .menu{ 
            display:none;
            position:absolute;
            top:67px;
            right:0px;
            background-color:#130000;
            border-bottom:4px solid #8a0000;
            width:100%;
            padding:10px;
            margin:0px;
        }
        .menu li{
            width:100%;
        }
        nav .menu li a{
            font-size: large;
            width:90%;
            height:32px;
            justify-content: left;
            align-items: left;
            margin:0;
            padding: 5px;
            border:1px solid rgba(121, 0, 0, 0.03);
            border-radius: 30px;
        }
        nav .menu-icon{
            cursor:pointer;
            float:right;
            padding:25px 20px;
            position:relative;
            user-select: none;
        }
        nav .menu-icon .nav-icon{
            background-color:#ffffff;
            display:block;
            height:2px;
            position:relative;
            transition: background 0.2s ease-out;
            width:18px;
        }
        nav .menu-icon .nav-icon:before,
        nav .menu-icon .nav-icon:after{
            background:rgb(255, 255, 255);
            content:'';
            display:block;
            height:100%;
            position:absolute;
            transition:all ease-out 0.2s;
            width:100%;
        }
        nav .menu-icon .nav-icon:before{
            top:5px;
        }
        nav .menu-icon .nav-icon:after{
            top:-5px;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon{
            background:transparent;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon:before{
            transform: rotate(-45deg);
            top:0;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon:after{
            transform: rotate(45deg);
            top:0;
        }
        nav .menu-btn{
            display:none;
        }
        nav .menu-btn:checked ~ .menu{
            display:block;
        }
}